document.addEventListener('DOMContentLoaded', function(){

    sharingCard();

    showOverlay();
    
    if (!window.matchMedia('(display-mode: fullscreen)').matches) {
        showCardInstall();
      }

});

var btnShare = document.querySelector('#share');
var bgShare = document.querySelector('.sharing-card');
var cardShare = document.querySelector('.card-share');
var closeShare = document.querySelector('.close-share');
var imgs = document.querySelectorAll('.gallery-image');
var container = document.querySelector('.container-overlay');
var cardInstall = document.querySelector('.card-pwa');
var hiddeInstall = document.querySelector('#later');
var isSafari = /^((?!chrome|Android).)*safari/i.test(navigator.userAgent);
var cardIphone = document.querySelector('.card-pwa-iphone');
var closeIphone = document.querySelector('#closeIphone');

function sharingCard(){
    btnShare.addEventListener('click', showShare);
}

function showShare(){
    bgShare.style.opacity = '0.9';
    cardShare.style.bottom = '7rem';

    hiddenCard();
}

function hiddenCard(){
    closeShare.addEventListener('click', hiddenShare);
}

function hiddenShare(){
    bgShare.style.opacity = '0.0';
    cardShare.style.bottom = '-150rem';
}

function showOverlay(){
    for (const img of imgs) {
        img.addEventListener('click', function(event) {
          showImage(event)
        })
      }
}

function showImage(e){
    var id = e.target.id;
    const imagen = document.createElement('IMG');
    imagen.src = `build/img/services/${id}.webp`;
    imagen.setAttribute('id', `${id}`);

/*     const overlay = document.createElement('DIV');
    container.appendChild(overlay); */
    container.appendChild(imagen);
    imagen.classList.add('overlay');
    bgShare.style.opacity = '0.9';
    bgShare.style.pointerEvents = 'unset';

    imagen.onclick = function(){
        cambiarImagen(imagen);
    }
    

    bgShare.onclick = function(){
        imagen.remove();
        bgShare.style.opacity = '0';
        bgShare.style.pointerEvents = 'none';
    }
}

function cambiarImagen(imagen){
    id=imagen.id;
    id++;
    if(id<=7){
        imagen.src = `build/img/services/${id}.webp`;
        imagen.setAttribute('id', `${id}`);
    }else{
        id=1
        imagen.src = `build/img/services/${id}.webp`;
        imagen.setAttribute('id', `${id}`);
    }
}

function showCardInstall(){
    if(isSafari === true){
        cardIphone.style.top = '15rem';
        bgShare.style.opacity = '0.9';
        bgShare.style.pointerEvents = 'unset';
    
        closeIphone.addEventListener('click', hiddeCardIphone);
    } else {
        cardInstall.style.top = '15rem';
        bgShare.style.opacity = '0.9';
        bgShare.style.pointerEvents = 'unset';
    
        hiddeInstall.addEventListener('click', hiddeCardInstall);
    }
}

function hiddeCardInstall(){
    cardInstall.style.top = '-40rem';
    bgShare.style.opacity = '0';
    bgShare.style.pointerEvents = 'none';
}

function hiddeCardIphone(){
    cardIphone.style.top = '-40rem';
    bgShare.style.opacity = '0';
    bgShare.style.pointerEvents = 'none';
}